import React from 'react';
import { Route } from 'react-router-dom';
import { DesktopDefaultPage, HomePage, SearchResults } from '../views';
import { Paths } from './Paths';

export const desktopRoutes = [
    // personal app
    <Route key={Paths.HomePage} path={Paths.HomePage} element={<HomePage />} />,
    <Route key={Paths.SearchResults} path={Paths.SearchResults} element={<SearchResults />} />,
    <Route key={Paths.DepartmentTeam} path={Paths.DepartmentTeam} element={<HomePage />} />,

    // home
    <Route key='*' path='*' element={<DesktopDefaultPage />} />,
];
