import { useTeamsContext } from '@addressbook/components-metaos-host';
import React from 'react';
import { QueryClient, QueryClientProvider, useQuery, UseQueryResult } from 'react-query';
import { initQuery, URIParam } from '../common/env';
import { ECSFlags, IECSConfigState } from './EcsFlags';
import { defaultECSFlags, getECSParams, loadEcsConfig } from './loadEcsConfig';

const ecsConfigQueryKey = 'GetECSConfigs';

const ecsQueryClient = new QueryClient({
    defaultOptions: {
        queries: {
            staleTime: Infinity,
            cacheTime: Infinity,
        },
    },
});

type ECSContextType = Partial<UseQueryResult<ECSFlags>>;
const ECSContext = React.createContext<ECSContextType>({ data: defaultECSFlags });

function ECSConfigProviderInner({
    children,
    defaultValue,
    value,
}: React.PropsWithChildren<{ defaultValue?: ECSFlags; value?: ECSFlags }>): JSX.Element {
    const userObjectId = initQuery.get(URIParam.UserId)!;
    const tid = initQuery.get(URIParam.TenantId)!;
    const ringId = initQuery.get(URIParam.RingId)!;
    const hostClientType = initQuery.get(URIParam.ClientType)!;
    const teamsContext = useTeamsContext();
    const ecsParams = getECSParams({
        userObjectId,
        tid,
        ringId,
        hostClientType,
        ...teamsContext,
    });
    const result = useQuery([ecsConfigQueryKey, ecsParams], (): Promise<IECSConfigState> => loadEcsConfig(ecsParams), {
        select: data => data.MWTWorkReport,
        placeholderData: { MWTWorkReport: defaultValue || defaultECSFlags, Headers: {} as any },
        enabled: !value && !!ecsParams,
    });

    return React.createElement(ECSContext.Provider, { value: value ? { data: value } : result }, children);
}

export function ECSConfigProvider({
    children,
    defaultValue,
    value,
}: React.PropsWithChildren<{ defaultValue?: ECSFlags; value?: ECSFlags }>): JSX.Element {
    return (
        <QueryClientProvider client={ecsQueryClient}>
            <ECSConfigProviderInner defaultValue={defaultValue} value={value}>
                {children}
            </ECSConfigProviderInner>
        </QueryClientProvider>
    );
}

export function useECSConfig(): ECSContextType {
    return React.useContext(ECSContext);
}
