/**
 * The options for the people select source.
 *
 * @property groupsInTenant - Whether to include groups in the tenant.
 * @property tagsInGroup - Whether to include tags in the group.
 * @property emptySuggestions - The suggestions to show when the input is empty.
 * @property fixedSuggestions - The suggestions to show at the top of the suggestions list, when input is not empty.
 * @property excludingSuggestions - The suggestions to exclude from the suggestions list.
 */
export interface PeopleSelectSourceOptions {
    groupsInTenant?: boolean;
    tagsInGroup?: boolean;
    excludingSuggestions?: PeopleSelection[];
    emptySuggestions?: PeopleSelection[];
    fixedSuggestions?: PeopleSelection[];
}

export interface UsePeopleSelectPropsOptions {
    sourceOptions?: PeopleSelectSourceOptions;
}

