/**
 * Retrieves the icon of a people selection. If the icon is not available, it returns undefined.
 * Mocked icons and icons from Graph API are supported.
 *
 * @param selection The people selection.
 * @param signal The abort signal.
 * @returns The icon of the people selection.
 */
export async function retrievePeopleSelectionIcon(
    selection: PeopleSelection,
    signal?: AbortSignal
): Promise<Blob | undefined> {
    // const mockedIcon = await retrieveMockedPeopleSelectionIcon(selection, signal);
    // if (mockedIcon !== null) return mockedIcon;

    return retrievePeopleSelectionIconByGraph(selection, signal);
}

async function retrievePeopleSelectionIconByGraph(
    selection: PeopleSelection,
    signal?: AbortSignal
): Promise<Blob | undefined> {
    const resource =
        selection.type === PeopleSelectionType.User
            ? graphServiceClient.users(selection.id)
            : selection.type === PeopleSelectionType.Group
            ? graphServiceClient.groups(selection.id)
            : undefined;

    if (!resource) return;

    try {
        return await resource.photos(64).download(signal);
    } catch {
        return;
    }
}

export async function retrievePeoplePresenceByGraph(id: string): Promise<string> {
    return await graphServiceClient.users(id).getPresence();
}

export async function retrievePeopleManagerByGraph(id: string): Promise<IPeople | undefined> {
    const user = await graphServiceClient.users(id).getManager();

    return mapUserToPeople(user);
}

async function retrieveMockedPeopleSelectionIcon(
    selection: PeopleSelection,
    signal?: AbortSignal
): Promise<Blob | undefined | null> {
    if (!mockedPeopleSelectionIconUrls.has(selection.id)) return null;

    const url = mockedPeopleSelectionIconUrls.get(selection.id);
    if (!url) return;

    try {
        const response = await fetch(url, { signal });
        return await response.blob();
    } catch {
        return;
    }
}

