import { useQuery, UseQueryOptions, UseQueryResult } from 'react-query';
import type { APIFunctions } from '../service/gen';
import type { RequestError } from '../service/RequestError';

/**
 * @param func service client API function
 * @param params service client API params
 * @param options UseQueryOptions
 */
function useReactQuery<T extends APIFunctions, TData = Awaited<ReturnType<T>>>(
    func: T,
    params: Parameters<T>[0],
    options?: Omit<UseQueryOptions<Awaited<ReturnType<T>>, RequestError, TData>, 'queryKey' | 'queryFn'>
): UseQueryResult<TData, RequestError> {
    return useQuery<Awaited<ReturnType<T>>, RequestError, TData>({
        queryFn: ({ queryKey, signal }): any => {
            // eslint-disable-next-line @typescript-eslint/no-unsafe-call
            return queryKey[1] ? (func as any)(queryKey[1], signal) : (func as any)(signal);
        },
        queryKey: [func.name, params],
        ...options,
    });
}

export { useReactQuery };
