import { useMemo, useState, useEffect } from 'react';
import { useReactiveVar } from 'react-reactive-var';
import { Subject } from 'rxjs';
import { Flag, flagDependency } from '../../common/flag';
import { modalShowingVar } from '../../reactiveVariables/CommonVars';

type FlagState = true | null;

/**
 * the state of component
 * true: visible
 * false: invisible(It has already been shown, or the preconditions are not met)
 */
type FlagStatus = true | false;

const FLAG_STORAGE_KEY = 'flag';

const states: { [key in Flag]?: FlagState } = {};

const subject = new Subject<Flag>();

function generateKey(key: string, suffix?: string): string {
    return `$_${key}${suffix ? `.${suffix}` : ''}`;
}
function getFlagTrue(flag: Flag): boolean {
    const storageKey = generateKey(FLAG_STORAGE_KEY, flag);
    if (!(flag in states)) states[flag] = localStorage.getItem(storageKey) ? true : null;
    return states[flag] === null;
}

function getFlagState(flag: Flag): FlagStatus {
    getFlagTrue(flag);
    if (flagDependency[flag]) {
        const isComplete = !flagDependency[flag]?.some(flag => getFlagTrue(flag));
        return isComplete ? !states[flag] : false;
    }
    return !states[flag];
}

function setFlagState(flag: Flag): void {
    states[flag] = true;
    const storageKey = generateKey(FLAG_STORAGE_KEY, flag);
    localStorage.setItem(storageKey, 'true');
    subject.next(flag);
}

/**
 * set
 *
 * @param flag flag of component meaning whether or not it can be displayed
 * @param autoCleanup cleanup the shown component when the window is closed
 * @returns [value, setValue]
 */
export function useFlagStatus(flag: Flag, autoCleanup: boolean): [isEnabled: FlagStatus, disable: () => void] {
    const [value, setValue] = useState<FlagStatus>(() => getFlagState(flag));
    const modalShowing = useReactiveVar(modalShowingVar);
    autoCleanup = !modalShowing && autoCleanup;

    useEffect(() => {
        const subscriber = subject.subscribe(updateFlag => {
            if (flagDependency[flag]?.includes(updateFlag)) {
                setValue(getFlagState(flag));
            }
        });
        return () => subscriber.unsubscribe();
    }, [flag]);

    useEffect(() => {
        if (autoCleanup) {
            const storageKey = generateKey(FLAG_STORAGE_KEY, flag);
            const callback = (): void => {
                if (getFlagState(flag)) localStorage.setItem(storageKey, 'true');
            };
            window.addEventListener('beforeunload', callback);
            return () => {
                window.removeEventListener('beforeunload', callback);
            };
        }
    }, [flag, autoCleanup]);

    return useMemo<[isEnabled: FlagStatus, disable: () => void]>(
        () => [
            !modalShowing && value,
            () => {
                setValue(false);
                setFlagState(flag);
            },
        ],
        [modalShowing, value, flag]
    );
}
