import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useSearchResultStyles = makeStyles(theme => ({
    root: { height: '100%', display: 'flex', flexDirection: 'column' },
    topSearchSection: {
        display: 'flex',
        justifyContent: 'flex-end',
        padding: '30px',
    },
    field: {
        flex: 1,
        '.ms-Label': {
            padding: '0 0 4px',
            whiteSpace: 'nowrap',
        },
    },
    peopleSection: {
        display: 'flex',
        alignItems: 'stretch',
        height: '100%',
        ...(isContrastTheme(theme.name) && {
            borderTop: `1px solid ${theme.palette.black}`,
            borderBottom: `1px solid ${theme.palette.black}`,
        }),
    },
    textFieldIcon1: {
        position: 'absolute',
        left: '6px',
        top: '50%',
        transform: 'translateY(-50%)',
        pointerEvents: 'none',
        // height: '20px',
        // width: '20px',
    },
    inputField: {
        flex: 1,
        '.ms-Label': {
            padding: '0 0 4px',
            whiteSpace: 'nowrap',
        },
        '.ms-TextField-field': {
            paddingLeft: '36px',
            backgroundColor: theme.palette.white,
            paddingRight: '36px',
        },
    },
    left: {
        flex: 1,
        width: 0,
        backgroundColor: theme.palette.neutralLighterAlt,
        borderRight: `1px solid ${
            isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiaryAlt
        }`,
    },
    main: {
        padding: '24px 80px 24px 80px',
        backgroundColor: theme.palette.white,
        height: '100%',
        overflowY: 'auto',
    },
    topText: {
        color: theme.palette.black,
    },
    top: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        // marginBottom: '20px',
    },
    dropdown: {
        width: '200px',
        marginBottom: '20px',
    },
    iconCell: {
        padding: 0,
        fontSize: '16px',
        // top: '2px',
        marginRight: '4px',
        height: '16px',
        width: '16px',
    },
}));
