import { makeStyles } from '@addressbook/components-fluentui-teams-theme';
import { ellipsisStyles } from '@addressbook/components-shared';

export const usePeopleListStyles = makeStyles(theme => ({
    iconCell: {
        padding: 0,
        fontSize: '16px',
        marginRight: '4px',
        height: '20px',
        width: '20px',
    },
    cellTitle: {
        display: 'flex',
        alignItems: 'center',
        gap: '4px',
        flexDirection: 'row',
    },
    nameShow: {
        display: 'flex',
        flexDirection: 'row',
        gap: '4px',
        alignItems: 'center',
        cursor: 'pointer',
    },
    nameField: {
        display: 'flex',
        alignItems: 'center',
        gap: '4px',
        flexDirection: 'row',
    },
    hoverShowButton: {
        height: '100%',
        width: '100%',
        padding: '0',
        ':hover,:focus-within': {
            color: theme.palette.themePrimary,
        },
    },
    hoverShowButtonContainer: {
        display: 'none',
        '*:hover > &, *:focus-within > &': {
            display: 'flex',
            position: 'relative',
            alignSelf: 'flex-end',
            left: '20px',
        },
    },
    nameSpan: [ellipsisStyles(1), { maxWidth: '160px' }],
    span: ellipsisStyles(1),
    list: {
        'ms-DetailsRow': {
            cursor: 'default',
        },
        '.ms-Nav': {
            transition: 'none !important' /* 禁用不必要的过渡效果 */,
        },

        // 'ms-DetailsList-contentWrapper': {
        //     overflowY: 'auto',
        // },
        'ms-DetailsHeader-cellTitle': { display: 'flex', alignItems: 'center', gap: '4px', flexDirection: 'row' },
    },
}));
