import axios, { AxiosError, AxiosRequestConfig } from 'axios';
import { ECS_BASIC_URL } from '../common/Constants';
import { logger, LogCategory } from '../logger';
import { ExtAxiosConfig, transformResponse } from '../service/axios';
import { ENVIRONMENT } from '../common/env';
import { ECSFlags, IECSConfigState, initialState } from './EcsFlags';

export type ECSParams = {
    agents: string;
    ClientEnvironment: string;
    TeamsRing: string;
    HostClientType: string;
    TenantID: string;
    UserID: string;
};

export function getECSParams(context?: {
    ringId?: string;
    hostClientType?: string;
    tid?: string;
    userObjectId?: string;
}): ECSParams | undefined {
    if (!context) {
        return undefined;
    }

    return {
        agents: 'MWTWorkReport',
        ClientEnvironment: ENVIRONMENT,
        TeamsRing: context.ringId || '',
        HostClientType: context.hostClientType || '',
        TenantID: context.tid || '',
        UserID: context.userObjectId || '',
    };
}

export const ECS_STORAGE_KEY = 'EcsConfig';
// try to get ECS config from local storage
const localeECS: Partial<ECSFlags> = (() => {
    try {
        return JSON.parse(localStorage.getItem(ECS_STORAGE_KEY) || '{}');
    } catch (error) {
        return {};
    }
})();

/**
 * default ECS config
 * merge with local storage ECS config
 */
export const defaultECSFlags = {
    ...initialState,
    ...localeECS,
};

export function loadEcsConfig(params?: ECSParams): Promise<IECSConfigState> {
    // add start time for performance logging
    const config: AxiosRequestConfig & ExtAxiosConfig = { params, _startRequestTime: performance.now() };

    return axios
        .get(ECS_BASIC_URL, config)
        .then(response => {
            const ecs = transformResponse<IECSConfigState>(response);
            // cache ecs config
            localStorage.setItem(ECS_STORAGE_KEY, JSON.stringify(ecs.MWTWorkReport));
            return ecs;
        })
        .catch((error: AxiosError): IECSConfigState => {
            logger.logTraceError(LogCategory.ECSRequest, `EcsCallError ${error?.config?.url} ${error.message}`);
            // fallback to default value
            return { MWTWorkReport: defaultECSFlags, Headers: {} as any };
        });
}
