import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';
import { INavStyleProps, INavStyles } from '@fluentui/react';

export const useHomePageStyles = makeStyles(theme => ({
    root: {
        height: '100%',
        overflowY: 'hidden',
        display: 'flex',
        flexDirection: 'column',
        backgroundColor: theme.palette.neutralLighterAlt,
    },
    topSearchSection: {
        display: 'flex',
        justifyContent: 'flex-end',
        padding: '5px 30px 5px 0',
    },

    peopleSection: {
        display: 'flex',
        alignItems: 'stretch',
        backgroundColor: theme.palette.white,
        height: '100%',
        // overflowY: 'auto',
        ...(isContrastTheme(theme.name) && {
            borderTop: `1px solid ${theme.palette.black}`,
            borderBottom: `1px solid ${theme.palette.black}`,
        }),
    },
    peopleList: {
        height: '80%',
        overflowY: 'auto',
        overflowX: 'hidden',
    },
    left: {
        flex: 1,
        width: 0,
        backgroundColor: theme.palette.neutralLighterAlt,
        borderRight: `1px solid ${
            isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiaryAlt
        }`,
    },
    main: {
        backgroundColor: theme.palette.white,
        flex: 4,
        width: 0,
        padding: '20px',
        columnGap: '20px',
        // overflowY: 'auto',
    },

    dropdown: {
        width: '400px',
        marginBottom: '20px',
        '.ms-Dropdown-caretDown': {
            fontSize: '16px',
            color: theme.palette.neutralPrimary,
        },
    },
}));
export const useNavStyles = (props: INavStyleProps): Partial<INavStyles> => ({
    chevronIcon: [
        {
            fontSize: 20,
            left: '4px',
            top: '10px',
        },
        props.isExpanded && {
            transform: '',
        },
        !props.isExpanded && {
            transform: 'rotate(-90deg)',
        },
    ],
});
