import '../styles/global.css';
import '../common/csp-inject';

import React from 'react';
import ReactDOM from 'react-dom';
import { TeamsHostProvider } from '@addressbook/components-metaos-host';
import { App } from '../views/App';
import { ErrorBoundary } from '../views/containers/ErrorBoundary';
import { init } from '../common/init';
import { logger, LogCategory, ScenarioName, StepName } from '../logger';

init(false);
ReactDOM.render(
    <ErrorBoundary>
        <TeamsHostProvider>
            <App />
        </TeamsHostProvider>
    </ErrorBoundary>,
    document.getElementById('root'),
    () => {
        logger.markScenario(ScenarioName.AppBoot, StepName.AppBootReactRendered);
        logger.logTraceInfo(LogCategory.AppBoot, 'React rendered desktop APP');
    }
);
