import { ITheme, setRTL } from '@fluentui/react';
import { getTeamsTheme } from '@addressbook/components-fluentui-teams-theme';
import { coerceTeamsTheme, useTeamsTheme } from '@addressbook/components-metaos-host';
import { useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { initQuery, isDevBrowser, URIParam } from '../common/env';
import { i18n } from '../localization';
import { LogCategory, logger } from '../logger';

/**
 * a hook to get ITheme with RTL support for FluentUI ThemeProvider
 *
 * @returns An ITheme object
 */
export function useFluentUIThemeWithRTL(isMobile?: boolean): ITheme {
    const themeName = useTeamsTheme() || initQuery.get(URIParam.Theme);
    const { i18n: currentI18n } = useTranslation(undefined, { useSuspense: false });
    const lang = currentI18n.resolvedLanguage || currentI18n.languages?.[0];

    useEffect(() => {
        if (isDevBrowser) {
            document.body.style.backgroundColor = '#F5F5F5';
        } else {
            document.body.style.backgroundColor = 'transparent';
        }
        if (themeName) {
            logger.logTraceInfo(LogCategory.AppBoot, `Rendered app host with theme: ${themeName}.`);
            const className = `is-${themeName}Theme`;
            document.body.classList.add(className);
            return () => document.body.classList.remove(className);
        }
    }, [themeName]);

    return useMemo(() => {
        const teamsTheme = getTeamsTheme(coerceTeamsTheme(themeName ?? ''), isMobile);
        if (lang) {
            const dir = i18n.dir();
            const rtl = dir === 'rtl';
            setRTL(rtl);
            return { ...teamsTheme, rtl };
        }
        return teamsTheme;
    }, [themeName, lang, isMobile]);
}
