import { User } from '@microsoft/microsoft-graph-types';
import { UserView } from '../service/query';

export function mapUserViewToPeople(user: UserView): IPeople {
    return {
        id: user.id,
        key: user.id,
        mail: user.mail ?? user.userPrincipalName,
        manager: undefined,
        name: user.displayName,
        officeLocation: user.officeLocation,
        phone: user.mobilePhone,
        role: user.jobTitle,
        departmentPaths: user.departmentPaths,
    };
}

export function mapUserToPeople(user: User): IPeople {
    return {
        id: user.id ?? '',
        key: user.id ?? '',
        mail: user.mail ?? user.userPrincipalName ?? '',
        manager: undefined,
        name: user.displayName ?? '',
        officeLocation: user.officeLocation ?? '',
        phone: user.mobilePhone ?? '',
        role: user.jobTitle ?? '',
        departmentPaths: {},
    };
}

export interface IPeople {
    key: string;
    id: string;
    name: string;
    mail: string;
    phone: string;
    role: string;
    officeLocation: string;
    manager: IPeople | undefined;
    departmentPaths: Record<string, string>;
}

export function getDepartmentIdFromFullId(fullId: string): string {
    return fullId?.split('/').pop() ?? '';
}

export function getDepartmentNameFromFullName(fullName: string): string {
    return fullName?.split('/').pop() ?? '';
}
