import { ActionButton } from '@fluentui/react';
import React from 'react';
import { clearQueryClientPersistor, queryClient } from '../../../service';

export const RefreshButton = (): JSX.Element => {
    const onClick = (): void => {
        // Clear the cache and refetch the data
        queryClient.clear(); // Clear the cache from memory
        clearQueryClientPersistor(); // Clear the cache from local storage
        window.location.reload();
        return;
    };
    return (
        <>
            <ActionButton iconProps={{ iconName: 'Refresh' }} onClick={onClick} />
        </>
    );
};
