import { ThemeProvider } from '@fluentui/react';
import React from 'react';
import { QueryClientProvider } from 'react-query';
import { ReactQueryDevtools } from 'react-query/devtools';
import { unstable_HistoryRouter as HistoryRouter, Routes } from 'react-router-dom';
import { STYLE_NONCE } from '../common/env';
import { ECSConfigProvider } from '../ecs';
import { useFluentUIThemeWithRTL } from '../hooks';
import { LogCategory, logger } from '../logger';
import { desktopRoutes } from '../routes/desktopRoutes';
import { history } from '../routes/history';
import { queryClient } from '../service';
import { themeProviderClassName } from '../styles';
import { LoadingIndicatorWrapper } from './common';
import { LoadingPage } from './components';

export const App: React.FC = (): JSX.Element => {
    const theme = useFluentUIThemeWithRTL();
    // Register a handler to flush out theme change and triggers a re-rendering immediately
    React.useEffect((): any => {
        // Component mounted
        logger.logTraceInfo(LogCategory.AppBoot, 'Rendered app host');
        return (): void => {
            // Component will unmount
            logger.logTraceInfo(LogCategory.AppBoot, 'Closing the app host');
        };
    }, []);

    return (
        <ThemeProvider theme={theme} applyTo='body' className={themeProviderClassName}>
            <React.Suspense fallback={<LoadingPage />}>
                <ECSConfigProvider>
                    <QueryClientProvider client={queryClient}>
                        <LoadingIndicatorWrapper />
                        <HistoryRouter history={history}>
                            <Routes>{desktopRoutes}</Routes>
                        </HistoryRouter>
                        <ReactQueryDevtools position='bottom-right' styleNonce={STYLE_NONCE} />
                    </QueryClientProvider>
                </ECSConfigProvider>
            </React.Suspense>
        </ThemeProvider>
    );
};
