/**
 * All enumerations of flag
 */
export enum Flag {
    OnboardingModal = 'OnboardingModal',
    WriteBubble = 'WriteBubble',
    RequestBubble = 'RequestBubble',
    SubmitBubble = 'SubmitBubble',
    ReviewBubble = 'ReviewBubble',
    SubmitGuidesBubble = 'SubmitGuidesBubble',
    ReviewGuidesBubble = 'ReviewGuidesBubble',
    TaskModuleHomeBubble = 'TaskModuleHomeBubble',
    TaskModuleHomeSimpleFRE = 'TaskModuleHomeSimpleFRE',
    TaskModuleHomeSimplePreviewBubble = 'TaskModuleHomeSimplePreviewBubble',
    TaskModuleHomeSimpleRuleSettingsBubble = 'TaskModuleHomeSimpleRuleSettingsBubble',
    MobileSubmitBubble = 'MobileSubmitBubble',
    MobileReviewBubble = 'MobileReviewBubble',
    FeedbackInAppBubble = 'FeedbackInAppBubble',
    FeedbackInTaskModuleBubble = 'FeedbackInTaskModuleBubble',
}

/**
 * Relationship binding
 * key: a flag that requires pre-items
 * value: all dependencies
 */
export const flagDependency: {
    [key in Flag]?: Flag[];
} = {
    [Flag.WriteBubble]: [Flag.OnboardingModal],
    [Flag.RequestBubble]: [Flag.WriteBubble, Flag.OnboardingModal],

    [Flag.SubmitBubble]: [Flag.RequestBubble, Flag.OnboardingModal],
    [Flag.SubmitGuidesBubble]: [Flag.SubmitBubble, Flag.OnboardingModal],

    [Flag.ReviewBubble]: [Flag.RequestBubble, Flag.OnboardingModal],
    [Flag.ReviewGuidesBubble]: [Flag.ReviewBubble, Flag.OnboardingModal],

    [Flag.FeedbackInAppBubble]: [Flag.SubmitGuidesBubble, Flag.OnboardingModal],

    [Flag.TaskModuleHomeBubble]: [Flag.OnboardingModal],

    [Flag.MobileSubmitBubble]: [Flag.OnboardingModal],
    [Flag.MobileReviewBubble]: [Flag.MobileSubmitBubble],
};
