import { getTeamsContext } from '@addressbook/components-metaos-host';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { generateURL, Paths } from '../../routes/Paths';
import { isNewChat } from '../../utilities/Utils';
import { SpecialErrorType } from '../../views/components/ErrorStatus';

/**
 * check is new chat and auto redirect to error page
 * will skip channel for channel ID
 */
export function useNewChatEffect(): void {
    const navigate = useNavigate();

    useEffect(() => {
        return () => {
            getTeamsContext().then(context => {
                const { chat, team, channel } = context || {};
                if (isNewChat(chat?.id, team?.groupId, channel?.id)) {
                    navigate(generateURL(Paths.HomePage, { errorType: SpecialErrorType.newChat }), {
                        replace: true,
                    });
                }
            });
        };
    }, [navigate]);
}
