import { useMemo } from 'react';
import { useSearchParams } from 'react-router-dom';
import type { AllPageParams, PageParams } from '../routes/Paths';

/**
 * a strong type wrapper for useSearchParams
 */
export function usePageParams<T extends keyof AllPageParams>(): {
    /**
     * params object
     */
    params: PageParams<T>;
    setParams: (
        nextInit: PageParams<T>,
        navigateOptions?: {
            replace?: boolean | undefined;
            state?: any;
        }
    ) => void;
    /**
     * reset the params
     */
    reset: () => void;
} {
    const [params, setParams] = useSearchParams();
    return useMemo(
        () =>
            ({
                params: Object.fromEntries(params),
                setParams,
                reset: () => setParams({}, { replace: true }),
            } as any),
        [params, setParams]
    );
}
