import { DefaultButton } from '@fluentui/react';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { usePageParams, useReactPageQuery } from '../../../hooks';
import { Strings } from '../../../localization';
import { ModuleName } from '../../../logger';
import { Paths } from '../../../routes/Paths';
import { SERVICE_CLIENT } from '../../../service';
import { mapUserViewToPeople } from '../../../utilities/peopleUtils';
import { EmptyStatus, ErrorStatus, LoadingPage } from '../../components';
import { AddressBookAppHeader } from '../../components/AddressBookAppHeader/AddressBookAppHeader';
import { PeopleList } from '../../components/PeopleList';
import { useSearchResultStyles } from './SearchResult.styles';

export const SearchResults = (): JSX.Element => {
    const styles = useSearchResultStyles();
    const iconProps = {
        iconName: 'Search',
        className: styles.textFieldIcon1,
        styles: {
            root: {
                fontSize: '20px',
            },
        },
    };
    const { t } = useTranslation();
    const { params } = usePageParams<Paths.SearchResults>();
    const { searchText } = params;

    const [inputValue, setInputValue] = useState(searchText);

    const onChange = React.useCallback((_: unknown, value?: string) => {
        setInputValue(value || '');
    }, []);
    const navigate = useNavigate();

    const {
        data: searchMembers,
        error: membersError,
        isLoading: membersLoading,
        ...membersQueryStatus
    } = useReactPageQuery(
        SERVICE_CLIENT.fetchSearchUser,
        { top: 10, userDisplayName: searchText },
        { enabled: searchText !== '' }
    );
    return (
        <div className={styles.root}>
            <AddressBookAppHeader moduleName={ModuleName.SearchResult} searchText={searchText} />
            {/* <div className={styles.topSearchSection}>
                <form noValidate autoComplete='off'>
                    <TextField
                        className={styles.inputField}
                        placeholder={t(Strings.searchPlaceholder)}
                        iconProps={iconProps}
                        value={inputValue}
                        onChange={onChange}
                        onKeyDown={event => {
                            if (event.key === 'Enter') {
                                navigate(
                                    generateURL(Paths.SearchResults, undefined, {
                                        searchText: inputValue,
                                    })
                                );
                            }
                        }}
                    />
                </form>
            </div> */}
            <div className={styles.main}>
                <div className={styles.top}>
                    <h2 className={styles.topText}>{t(Strings.searchResultLabel, { searchResult: searchText })}</h2>
                    <DefaultButton onClick={() => navigate(Paths.DepartmentTeam)} text={t(Strings.exitSearchLabel)} />
                </div>

                {searchMembers && searchMembers.length > 0 && (
                    <PeopleList
                        items={searchMembers.map(data => mapUserViewToPeople(data))}
                        queryStatus={membersQueryStatus}
                        moduleName={ModuleName.SearchResult}
                    />
                )}
                {searchMembers?.length === 0 && (
                    <EmptyStatus
                        title={t(Strings.errorTitleForNotContract)}
                        message={t(Strings.errorMessageForNotContract)}
                        img='NoMessage'
                    />
                )}
                {membersLoading && <LoadingPage />}
                {membersError && <ErrorStatus error={membersError} />}
            </div>
        </div>
    );
};
