import { getAuthToken, getTeamsContext } from '@addressbook/components-metaos-host';
import { PeopleSelection, PeopleSelectionType } from '@addressbook/components-people';
import jwtDecode from 'jwt-decode';
import { useQuery } from 'react-query';
import { ADDRESSBOOK_APP_RESOURCE_URL } from '../common/env';
import { UserPayload } from '../utilities';

const ANYONE_SELECTION_ID = 'anyone';

/**
 * A hook that returns the current user selection.
 *
 * @returns The current user selection.
 */
export function useCurrentUserSelection(): PeopleSelection | undefined {
    const { data } = useQuery<PeopleSelection>({
        queryKey: getCurrentUserSelection.name,
        queryFn: getCurrentUserSelection,
        staleTime: Infinity,
    });

    return data;
}

async function getCurrentUserSelection(): Promise<PeopleSelection> {
    const teamsContext = await getTeamsContext();

    const user = teamsContext.user;
    if (!user) throw new Error('User not found in Teams context.');

    const selection = {
        type: PeopleSelectionType.User,
        id: user.id,
        name: user.displayName ?? '',
        mail: user.userPrincipalName,
    };

    // Patch name if absent in Teams context.
    // We need remove the fallback after the issue has been fixed by the Teams. Relevant issue discussion: https://github.com/OfficeDev/microsoft-teams-library-js/issues/2330
    if (!selection.name) {
        const token = await getAuthToken(ADDRESSBOOK_APP_RESOURCE_URL);
        const jwt = jwtDecode<UserPayload>(token);
        if (jwt && jwt.name) selection.name = jwt.name;
    }

    return selection;
}

