    //     return departmentFullId === undefined
    //         ? items
    //         : items.filter(item => item.departs.filter(depart => depart.includes(departmentFullId)).length > 0);
    // }, [departmentFullId, items]);

    // const reportSlotResult = useReactPageQuery(
