    const options: IDropdownOption[] = [
        { key: MemberType.AllMember, text: t(Strings.dropDownForDepartDefault) },
        { key: MemberType.DirectMember, text: t(Strings.dropDownForDepartOption1) },
    ];

    // const [filteredItems, setFilteredItems] = useState<IPeople[]>();

    // useEffect(() => {
    //     setFilteredItems(departmentMembers);
    // }, [departmentMembers]);

    const [selectedMemberType, setSelectedMemberType] = React.useState<MemberType>(MemberType.AllMember);

    const onOptionChange = (event: React.FormEvent<HTMLDivElement>, option?: IDropdownOption): void => {
        if (option !== undefined) {
            setSelectedMemberType(option.key as MemberType);
        }
    };

    const {
        data: navData,
        isLoading: loadingNav,
        error: navDataError,
    } = useReactQuery(
        SERVICE_CLIENT.fetchDepartmentNav,
        {},
        {
            staleTime: 1 * 30 * 60 * 1000, // 30 minutes
            cacheTime: 1 * 30 * 60 * 1000, // 30 minutes
        }
    );

    const currentDepartmentId = departmentFullId ? getDepartmentIdFromFullId(departmentFullId) : navData?.id;

    const {
        data: members,
        error: membersError,
        isLoading: membersLoading,
        ...membersQueryStatus
    } = useMemberListByDepartment(selectedMemberType, currentDepartmentId === navData?.id, currentDepartmentId, 20);

    const navLinkGroups = React.useMemo(() => {
        return navData ? [{ links: [mapDepartmentToNavLink(navData, '', '', departmentFullName)] }] : [];
    }, [navData, departmentFullName]);

    useEffect(() => {
        if ((!departmentFullId || departmentFullId === '') && navData?.id && navData?.name) {
            navigate(
                generateURL(Paths.DepartmentTeam, undefined, {
                    departmentFullName: navData.name,
                    departmentFullId: navData.id,
                })
            );
        }
    }, [departmentFullId, members, navData?.id, navData?.name, navigate]);

    return (
        <div className={styles.root}>
            <AddressBookAppHeader moduleName={ModuleName.HomePage} />

            <div className={styles.peopleSection}>
                <div className={styles.left}>
                    {loadingNav && <LoadingPage />}
                    {navData && (
                        <Nav
                            ariaLabel=''
                            styles={useNavStyles}
                            groups={navLinkGroups}
                            selectedKey={departmentFullId}
                            // onLinkExpandClick={}
                            onLinkClick={(ev, item) => {
                                ev?.preventDefault(); // 阻止默认的 URL 跳转行为
                                if (item?.url) {
                                    navigate(item.url); // 使用 React Router 的路由跳转
                                }
                            }}
                        />
                    )}
                    {navDataError && <div> {navDataError?.message} </div>}
                </div>
                <div className={styles.main}>
                    <h2>{departmentFullName ?? navData?.name}</h2>
                    <div>
                        <Dropdown
                            className={styles.dropdown}
                            placeholder='Select an option'
                            options={options}
                            selectedKey={selectedMemberType}
                            onChange={onOptionChange}
                        />
                    </div>
                    <div className={styles.peopleList}>
                        {members && members.length > 0 && (
                            <PeopleList
                                items={members.map(data => mapUserViewToPeople(data))}
                                queryStatus={membersQueryStatus}
                                moduleName={ModuleName.HomePage}
                            />
                        )}
                        {members?.length === 0 && (
                            <EmptyStatus
                                title={t(Strings.errorTitleForNotContract)}
                                message={t(Strings.errorMessageForNotContract)}
                                img='NoMessage'
                            />
                        )}
                        {membersLoading && <LoadingPage />}
                        {membersError && <ErrorStatus error={membersError} />}
                    </div>
                </div>
            </div>
        </div>
    );
