import { INavLink } from '@fluentui/react';
import { UseInfiniteQueryResult } from 'react-query';
import { useReactPageQuery, UseReactPageQueryResult } from '../../../hooks';
import { generateURL, Paths } from '../../../routes/Paths';
import { DepartmentView, RequestError, SERVICE_CLIENT } from '../../../service';
import { UserView } from '../../../service/query';

export function mapDepartmentToNavLink(
    department: DepartmentView,
    currentDepartmentId: string, // 用于路由生成 V1
    currentDepartmentName: string, // 用于路由生成 V2
    selectedDepartmentName: string // 用于判断当前部门是否展开
): INavLink {
    const departmentFullId = currentDepartmentId === '' ? department.id : `${currentDepartmentId}/${department.id}`;
    // departmentFullId 是当前部门的完整 ID，用于生成 URL， 标识哪一页做expand，标识当前部门
    // 因为在HomePage的PeopleList里，不支持用<Guid, <string, string>>的结构来表明DepartmentId, DepartmentFullName, DepartmentFullId,所以路由改换成使用DepartmentFullName和DepartmentId

    // 更新：v3使用departmentFullId和departmentFullName，其中key是departmentFullId，好处是不用担心FullName的唯一性和特殊字符，以及拿到最全的数据，FullId表示层级结构，FullName表示部门名称
    const departmentFullName =
        currentDepartmentName === '' ? department.name : `${currentDepartmentName}/${department.name}`;
    const link: INavLink = {
        name: department.name,
        url: generateURL(Paths.DepartmentTeam, undefined, {
            departmentFullName,
            departmentFullId,
        }),
        key: departmentFullId,
        expandAriaLabel: department.name,
        isExpanded: selectedDepartmentName?.includes(department.name),
        // isExpanded: true,
        links: department.subDepartments.length
            ? department.subDepartments.map(subDepartment =>
                  mapDepartmentToNavLink(subDepartment, departmentFullId, departmentFullName, selectedDepartmentName)
              )
            : undefined, // 如果有子部门，则递归处理；否则为 undefined
    };
    return link;
}

/**
 * hook for get user member list
 *
 * @param type is all member list or direct member list
 * @param top page query page size
 * @returns page query result
 */
export function useMemberListByDepartment(
    type: MemberType,
    isRootDepartment: boolean,
    departmentId?: string,
    top?: number,
    skipToken?: string
): Omit<UseInfiniteQueryResult<UserView[], RequestError>, 'data'> & UseReactPageQueryResult<UserView[]> {
    const isAllMember = type === 'AllMember';
    const func = isAllMember
        ? SERVICE_CLIENT.fetchGetAllUserMembersByDepartment
        : SERVICE_CLIENT.fetchGetDirectUserMembersByDepartment;

    const params = {
        departmentId,
        isRootDepartment,
        top,
        skipToken,
    };
    return useReactPageQuery(func, params, { enabled: !!departmentId });
}

// export type MemberType = 'AllMember' | 'DirectMember';

export enum MemberType {
    AllMember = 'AllMember',
    DirectMember = 'DirectMember',
}
