import { PeopleCoin } from '@addressbook/components-people';
import { ActionButton, DirectionalHint, ITooltipProps, Link, TooltipDelay, TooltipHost } from '@fluentui/react';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { retrievePeopleSelectionIcon } from '../../../hooks';
import { Strings } from '../../../localization';
import { logger, ModuleName, ScenarioName, ScenarioType } from '../../../logger';
import { generateURL, Paths } from '../../../routes/Paths';
import { getDepartmentNameFromFullName, IPeople } from '../../../utilities/peopleUtils';
import { OpenCallById, OpenChatById } from '../../../utilities/quickAppUtils';
import { CommonDetailsList } from '../../common';
import { PeopleCard } from '../PeopleCard';
import { usePeopleListStyles } from './PeopleList.styles';

interface PeopleListProps {
    items: IPeople[] | undefined;
    moduleName: ModuleName;
    queryStatus?: { isLoading?: boolean; canLoadMore?: boolean; loadMore?: () => void };
}

export const PeopleList = ({ items, queryStatus }: PeopleListProps): JSX.Element => {
    const styles = usePeopleListStyles();

    // People Card:
    const [calloutTarget, setCalloutTarget] = useState<HTMLElement | undefined>(undefined);
    const [peopleShow, setPeopleShow] = useState<IPeople | undefined>(undefined);
    const [openCardId, setOpenCardId] = useState('');

    const navigate = useNavigate();
    const { t } = useTranslation();
    // const tooltipId = useId('tooltip');
    const columns = [
        {
            key: 'name',
            name: t(Strings.peopleListColumnForName),
            fieldName: 'name',
            // minWidth: 200,
            // maxWidth: 300,
            minWidth: 264,
            maxWidth: 264,
            isResizable: false,
            iconName: 'ContactCard',
            iconClassName: styles.iconCell,
            className: styles.cellTitle,
            onRender: (item: IPeople) => {
                return (
                    <div className={styles.nameField}>
                        <div
                            className={styles.nameShow}
                            // When Click the user name, show the PeopleCard
                            onClick={event => {
                                setOpenCardId(item.id);
                                const targetElement = event.target as HTMLElement;
                                setPeopleShow(item);
                                setCalloutTarget(targetElement);
                                logger.logUserBiAction(
                                    ScenarioName.PeopleCard,
                                    ScenarioType.OpenPeopleCard,
                                    'moduleName',
                                    {}
                                );
                            }}>
                            <PeopleCoin
                                selection={{ id: item.id, name: item.name, type: 0 }}
                                onRetrieveIcon={retrievePeopleSelectionIcon}
                                coinSize={24}
                            />
                            <div className={styles.nameSpan} title={item.name}>
                                {item.name}
                            </div>
                        </div>
                        <div className={styles.hoverShowButtonContainer}>
                            <ActionButton
                                iconProps={{
                                    iconName: 'Chat',
                                    styles: {
                                        root: {
                                            fontSize: '20px',
                                        },
                                    },
                                }}
                                title='Chat'
                                ariaLabel='Chat'
                                className={styles.hoverShowButton}
                                onClick={() => OpenChatById(item.id)}
                            />
                            <ActionButton
                                iconProps={{
                                    iconName: 'Call',
                                    styles: {
                                        root: {
                                            fontSize: '20px',
                                            padding: 0,
                                        },
                                    },
                                }}
                                title='Emoji'
                                ariaLabel='Emoji'
                                className={styles.hoverShowButton}
                                onClick={() => OpenCallById(item.id)}
                            />
                        </div>
                    </div>
                );
            },
        },
        {
            key: 'mail',
            name: t(Strings.peopleListColumnForEmail),
            fieldName: 'mail',
            minWidth: 200,
            maxWidth: 300,
            isResizable: false,
            iconName: 'Mail',
            iconClassName: styles.iconCell,
            onRender: (item: IPeople) => {
                return (
                    <div title={item.mail} className={styles.span}>
                        {item.mail}
                    </div>
                );
            },
        },
        {
            key: 'phone',
            name: t(Strings.peopleListColumnForPhone),
            fieldName: 'phone',
            minWidth: 100,
            width: 120,
            maxWidth: 150,
            isResizable: false,
            iconName: 'Call',
            iconClassName: styles.iconCell,
            onRender: (item: IPeople) => {
                return (
                    <div title={item.phone} className={styles.span}>
                        {item.phone}
                    </div>
                );
            },
        },
        {
            key: 'depart',
            name: t(Strings.peopleListColumnForDepart),
            fieldName: 'depart',
            minWidth: 200,
            maxWidth: 300,
            isResizable: false,
            iconName: 'Location',
            iconClassName: styles.iconCell,
            onRender: (item: IPeople) => {
                const tooltipProps: ITooltipProps = {
                    onRenderContent: () => (
                        <ol style={{ margin: '0 5px 0 0', padding: '0 0 0 20px' }}>
                            {Object.entries(item.departmentPaths).map(([_departmentFullId, departmentFullName]) => (
                                <li>{departmentFullName}</li>
                            ))}
                        </ol>
                    ),
                };

                return (
                    <>
                        <TooltipHost
                            tooltipProps={tooltipProps}
                            delay={TooltipDelay.zero}
                            // id={tooltipId}
                            directionalHint={DirectionalHint.leftCenter}>
                            <div>
                                {Object.entries(item.departmentPaths).map(
                                    ([departmentFullId, departmentFullName], index) => (
                                        <>
                                            <Link
                                                key={departmentFullId} // 使用 url 作为唯一 key
                                                onClick={() =>
                                                    navigate(
                                                        generateURL(Paths.DepartmentTeam, undefined, {
                                                            departmentFullName,
                                                            departmentFullId,
                                                        })
                                                    )
                                                }>
                                                {getDepartmentNameFromFullName(departmentFullName)}
                                            </Link>
                                            {index < Object.entries(item.departmentPaths).length - 1 && <span>; </span>}
                                        </>
                                    )
                                )}
                            </div>
                        </TooltipHost>
                    </>
                );
            },
        },
        {
            key: 'role',
            name: t(Strings.peopleListColumnForJob),
            fieldName: 'role',
            minWidth: 100,
            width: 120,
            maxWidth: 150,
            isResizable: true,
            iconName: 'Desk',
            iconClassName: styles.iconCell,
            onRender: (item: IPeople) => {
                return (
                    <div title={item.role} className={styles.span}>
                        {item.role}
                    </div>
                );
            },
        },
    ];

    return (
        <>
            <CommonDetailsList
                className={styles.list}
                columns={columns}
                items={items}
                queryStatus={queryStatus}
                shimmerLines={10}
            />
            {peopleShow !== undefined && peopleShow.id === openCardId && (
                <PeopleCard people={peopleShow} onDismiss={() => setOpenCardId('')} target={calloutTarget} />
            )}
        </>
    );
};
