import { PeopleCoin } from '@addressbook/components-people';
import { ActionButton, Callout, FontIcon, mergeStyles, Target } from '@fluentui/react';
import React from 'react';
import { PRESENCE_ICONS } from '../../../common/icons';
import {
    retrievePeopleManagerByGraph,
    retrievePeoplePresenceByGraph,
    retrievePeopleSelectionIcon,
} from '../../../hooks';
import { IPeople } from '../../../utilities/peopleUtils';
import { OpenCallById, OpenChatById, OpenOrgExplorerApp, OpenVideoCallById } from '../../../utilities/quickAppUtils';
import { usePeopleCardStyles } from './PeopleCard.styles';

interface PeopleCardProps {
    people: IPeople;
    onDismiss: () => void;
    target?: HTMLElement | Target;
}

export const PeopleCard = ({ people, onDismiss, target }: PeopleCardProps): JSX.Element => {
    const peopleCardStyles = usePeopleCardStyles();
    const [presence, setPresence] = React.useState<string | undefined>(undefined);
    const [manager, setManager] = React.useState<IPeople | undefined>(undefined);
    React.useEffect(() => {
        const fetchPresence = async (): Promise<void> => {
            const presence = await retrievePeoplePresenceByGraph(people.id);
            setPresence(presence);
        };
        fetchPresence();
    }, [people.id]);

    React.useEffect(() => {
        const fetchPresence = async (): Promise<void> => {
            const manager = await retrievePeopleManagerByGraph(people.id);
            setManager(manager);
        };
        fetchPresence();
    }, [people.id]);

    return (
        <Callout
            role='dialog'
            className={peopleCardStyles.callout}
            gapSpace={20}
            target={target}
            isBeakVisible={false}
            onDismiss={onDismiss}
            setInitialFocus>
            <div className={peopleCardStyles.root}>
                <div className={peopleCardStyles.info}>
                    <div className={peopleCardStyles.peopleWithIcon}>
                        <div className={peopleCardStyles.peopleWithPresenceInfo}>
                            <PeopleCoin
                                selection={{ id: people.id, name: people.name, type: 0 }}
                                onRetrieveIcon={retrievePeopleSelectionIcon}
                                coinSize={64}
                            />
                            {presence && (
                                <div className={peopleCardStyles.presenceInfo}>
                                    <FontIcon
                                        aria-label='Mail'
                                        iconName={PRESENCE_ICONS[presence].name}
                                        className={mergeStyles(peopleCardStyles.icon, {
                                            color: PRESENCE_ICONS[presence].color,
                                        })}
                                    />
                                </div>
                            )}
                        </div>
                        <div className={peopleCardStyles.info}>
                            <span className={peopleCardStyles.boldText}>{people.name}</span>
                            <span>{people.role}</span>
                            <span>
                                {Object.entries(people.departmentPaths)
                                    .map(([_, value]) => value)
                                    .join('; ')}
                            </span>
                        </div>
                    </div>
                    <div className={peopleCardStyles.quickHeader}>
                        <ActionButton
                            iconProps={{
                                iconName: 'Chat',
                                styles: {
                                    root: {
                                        fontSize: '20px',
                                    },
                                },
                            }}
                            title='Chat'
                            ariaLabel='Chat'
                            // className={styles.hoverShowButton}
                            onClick={() => OpenChatById(people.id)}
                        />
                        <ActionButton
                            iconProps={{
                                iconName: 'Organization',
                                styles: {
                                    root: {
                                        fontSize: '20px',
                                    },
                                },
                            }}
                            title='OpenOrgExplorerApp'
                            ariaLabel='OpenOrgExplorerApp'
                            onClick={() => OpenOrgExplorerApp()}
                        />
                        <ActionButton
                            iconProps={{
                                iconName: 'Call',
                                styles: {
                                    root: {
                                        fontSize: '20px',
                                        padding: 0,
                                    },
                                },
                            }}
                            title='OpenCall'
                            ariaLabel='OpenCall'
                            // className={styles.hoverShowButton}
                            onClick={() => OpenCallById(people.id)}
                        />
                        <ActionButton
                            iconProps={{
                                iconName: 'Video',
                                styles: {
                                    root: {
                                        fontSize: '20px',
                                    },
                                },
                            }}
                            title='OpenVideoCall'
                            ariaLabel='OpenVideoCall'
                            // className={styles.hoverShowButton}
                            onClick={() => OpenVideoCallById(people.id)}
                        />
                    </div>
                    <div className={peopleCardStyles.divider}></div>
                </div>
                <div className={peopleCardStyles.main}>
                    {presence && (
                        <div className={peopleCardStyles.presence}>
                            <FontIcon
                                aria-label='Mail'
                                iconName={PRESENCE_ICONS[presence].name}
                                className={mergeStyles(peopleCardStyles.icon, {
                                    color: PRESENCE_ICONS[presence].color,
                                })}
                            />
                            <span className={peopleCardStyles.subHeaderText}>{presence}</span>
                        </div>
                    )}
                    <div className={peopleCardStyles.subHeader}>
                        <span className={peopleCardStyles.subHeaderText}>联系人</span>
                        <FontIcon aria-label='ChevronRight' iconName='ChevronRight' className={peopleCardStyles.icon} />
                    </div>
                    {people.mail && (
                        <div className={peopleCardStyles.ContractInfo}>
                            <FontIcon aria-label='Mail' iconName='Mail' className={peopleCardStyles.icon} />
                            <span>{people.mail}</span>
                        </div>
                    )}
                    {people.phone && (
                        <div className={peopleCardStyles.ContractInfo}>
                            <FontIcon aria-label='Call' iconName='Call' className={peopleCardStyles.icon} />
                            <span>{people.phone}</span>
                        </div>
                    )}

                    {people.officeLocation && (
                        <div className={peopleCardStyles.ContractInfo}>
                            <FontIcon aria-label='Location' iconName='Location' className={peopleCardStyles.icon} />
                            <span>{people.officeLocation}</span>
                        </div>
                    )}
                    {manager && (
                        <>
                            <div className={peopleCardStyles.subHeader}>
                                <span className={peopleCardStyles.subHeaderText}>直属领导</span>
                                <FontIcon
                                    aria-label='ChevronRight'
                                    iconName='ChevronRight'
                                    className={peopleCardStyles.icon}
                                />
                            </div>
                            <div className={peopleCardStyles.peopleWithIcon}>
                                <div className={peopleCardStyles.info}>
                                    <PeopleCoin
                                        selection={{ id: manager.id, name: manager.name, type: 0 }}
                                        onRetrieveIcon={retrievePeopleSelectionIcon}
                                        coinSize={32}
                                    />
                                </div>
                                <div>
                                    <span>{manager.name}</span>
                                    <br></br>
                                    <span>{manager.role}</span>
                                </div>
                            </div>
                        </>
                    )}
                </div>
            </div>
        </Callout>
    );
};
