import { useEffect, useState } from 'react';
import { useDataFlag } from './useDataFlag';
import { ListComponentStatus } from './useListComponentStatusChange';

export type ReportListType = 'Received' | 'Submitted' | 'ComposeExtension';

/**
 * hook for get report list actions
 *
 * @param reportListType is submitted report list or received report list
 * @returns actions
 */
export function useTabContentAction(
    reportListType: ReportListType,
    hasExtraList?: boolean
): {
    setExtraCardListStatus: (status: ListComponentStatus) => void;
    setTimeSensitiveCardListStatus: (status: ListComponentStatus) => void;
    setAdhocCardListStatus: (status: ListComponentStatus) => void;
    setReportListStatus: (status: ListComponentStatus) => void;
    showLoading: boolean;
    isAllEmpty: boolean;
} {
    const { hasFlag, setFlag, delFlag } = useDataFlag(reportListType);

    const [extraCardListStatus, setExtraCardListStatus] = useState<ListComponentStatus>();
    const [timeSensitiveCardListStatus, setTimeSensitiveCardListStatus] = useState<ListComponentStatus>();
    const [adhocCardListStatus, setAdhocCardListStatus] = useState<ListComponentStatus>();
    const [reportListStatus, setReportListStatus] = useState<ListComponentStatus>();

    const statusList = [];
    if (hasExtraList) statusList.push(extraCardListStatus);
    statusList.push(timeSensitiveCardListStatus, adhocCardListStatus);
    if (reportListType !== 'ComposeExtension') statusList.push(reportListStatus);

    const isAllEmpty = statusList.every(s => s === ListComponentStatus.Empty);
    const hasNonEmpty = statusList.some(s => s === ListComponentStatus.NonEmpty);
    const hasError = statusList.some(s => s === ListComponentStatus.Error);
    const showLoading = !(hasFlag || isAllEmpty || hasNonEmpty || hasError);

    useEffect(() => {
        if (isAllEmpty && hasFlag) delFlag();
        if (hasNonEmpty && !hasFlag) setFlag();
    }, [delFlag, hasNonEmpty, hasFlag, isAllEmpty, setFlag]);

    return {
        setExtraCardListStatus,
        setTimeSensitiveCardListStatus,
        setAdhocCardListStatus,
        setReportListStatus,
        showLoading,
        isAllEmpty,
    };
}
