import { EmptyState } from '@addressbook/components-shared';
import { mergeStyles, PrimaryButton, useTheme } from '@fluentui/react';
import React, { useMemo } from 'react';

import FileEmpty from '../../../assets/emptyStatus/file-empty.svg';
import NoMessage from '../../../assets/emptyStatus/no-message.svg';
import NoUnsubmitted from '../../../assets/emptyStatus/no-unsubmitted.svg';
import Optimizing from '../../../assets/emptyStatus/optimizing.svg';
import Submitted from '../../../assets/emptyStatus/submitted.svg';
import { useEmptyStatusStyles } from './EmptyStatus.styles';

export interface EmptyStatusProps {
    img?: 'FileEmpty' | 'Optimizing' | 'Submitted' | 'NoUnsubmitted' | 'NoMessage'; // c:\teams-verticals-workreport\Source\Clients\web-apps\WorkReport\src\assets\emptyStatus\no-message.svg
    title?: string;
    message?: string;
    actionText?: string;
    isSmall?: boolean;
    onAction?: () => void;
    className?: string;
    role?: React.AriaRole;
}

export const EmptyStatus = ({
    img,
    title,
    message,
    actionText,
    onAction,
    className,
    isSmall,
    children,
    role,
}: React.PropsWithChildren<EmptyStatusProps>): JSX.Element => {
    const theme = useTheme();
    const styles = useEmptyStatusStyles();
    const imgFile = useMemo(
        () =>
            img === 'FileEmpty'
                ? FileEmpty
                : img === 'Optimizing'
                ? Optimizing
                : img === 'Submitted'
                ? Submitted
                : img === 'NoUnsubmitted'
                ? NoUnsubmitted
                : img === 'NoMessage'
                ? NoMessage
                : '',
        [img]
    );

    return (
        <EmptyState
            role={role}
            className={mergeStyles(styles.root, isSmall && 'is-small', className)}
            image={imgFile}
            title={title ?? ''}
            message={message}
            height='100%'
            background='transparent'
            color={theme.palette.black}>
            {actionText && <PrimaryButton text={actionText} onClick={onAction} />}
            {children}
        </EmptyState>
    );
};
