import { TextField } from '@fluentui/react';
import React, { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { Strings } from '../../../localization';
import { generateURL, Paths } from '../../../routes/Paths';
import { useSearchFieldStyles } from './SearchPeopleField.styles';

export interface SearchPeopleFieldProps {
    searchText?: string;
}

export const SearchPeopleField = ({ searchText }: SearchPeopleFieldProps): JSX.Element => {
    const { t } = useTranslation();
    const [inputValue, setInputValue] = useState(searchText || '');
    const onChange = useCallback((_: unknown, value?: string) => {
        setInputValue(value || '');
    }, []);

    const styles = useSearchFieldStyles();
    const navigate = useNavigate();

    const iconProps = {
        iconName: 'Search',
        className: styles.textFieldIcon1,
        styles: {
            root: {
                fontSize: '20px',
            },
        },
    };
    return (
        <>
            <form noValidate autoComplete='off'>
                <TextField
                    className={styles.inputField}
                    placeholder={t(Strings.searchPlaceholder)}
                    iconProps={iconProps}
                    value={inputValue}
                    onChange={onChange}
                    onKeyDown={event => {
                        if (event.key === 'Enter') {
                            navigate(
                                generateURL(Paths.SearchResults, undefined, {
                                    searchText: inputValue,
                                })
                            );
                        }
                    }}
                />
            </form>
        </>
    );
};
