/**
 * flags here that you specify in ECS
 * Make sure to copy over the state specified in this field over to the initialState down below as well.
 */
// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface ECSFlags {
    /**
     * NPS Feedback Enabled Time Range, such as 2024-01-31/2024-02-08
     */
    NPSFeedbackEnabledTimeRange?: string;

    /**
     * Feedback bubble enabled Time Range, such as 2024-01-31/2024-02-08
     */
    FeedbackBubbleEnabledTimeRange?: string;
}

export interface EcsHeaders {
    CountryCode: string;
    ETag: string;
    Expires: string;
    StatusCode: string;
}
export interface EcsResponseBase {
    Headers: EcsHeaders;
}

export interface IECSConfigState extends EcsResponseBase {
    MWTWorkReport: ECSFlags;
}

export enum EcsConfigType {
    Default = 0,
    User = 1,
}

export interface EcsConfig<T extends EcsResponseBase> {
    config: T;
    configType: EcsConfigType;
    eTag: string;
    expiration: number;
    cacheUpdateTime: number;
    lastFetchTokenHash: number | undefined;
    appVersion: string | undefined;
}

/**
 * initial state to be used when ECS fetch fails.
 * Note: Please change the initialState after your feature complete rollout on ECS
 */
export const initialState: Required<ECSFlags> = {
    NPSFeedbackEnabledTimeRange: '',
    FeedbackBubbleEnabledTimeRange: '2024-09-18/2024-10-10',
};
