import { CommonLoadingIndicator } from '@addressbook/components-shared/src/CommonLoadingIndicator';
import React from 'react';
import { useIsFetching } from 'react-query';
import { Paths } from '../../../routes/Paths';

export interface LoadingIndicatorWrapperProps {
    isMobile?: boolean;
}

export const LoadingIndicatorWrapperComponent: React.FC<LoadingIndicatorWrapperProps> = ({ isMobile }): JSX.Element => {
    const isFetching = useIsFetching() > 0;
    const hideProgressIndicator = !isMobile && window.location.pathname.indexOf(Paths.HomePage) > 0;

    return <>{!hideProgressIndicator && <CommonLoadingIndicator isFetching={isFetching} />}</>;
};
