import { CommonIcon } from '@addressbook/components-shared';
import {
    Announced,
    ConstrainMode,
    DetailsHeader,
    DetailsListLayoutMode,
    DetailsRow,
    IColumn,
    IDetailsHeaderProps,
    IDetailsRowProps,
    IGroup,
    IGroupRenderProps,
    mergeStyles,
    SelectionMode,
    ShimmeredDetailsList,
} from '@fluentui/react';
import React from 'react';
import useInfiniteScroll from 'react-infinite-scroll-hook';

export interface CommonDetailsListProps<T> {
    items?: T[];
    columns?: IColumn[];
    className?: string;
    queryStatus?: { isLoading?: boolean; canLoadMore?: boolean; loadMore?: () => void };
    ariaLabel?: string;
    shimmerLines?: number;
    rowClass?: (props: IDetailsRowProps) => string;
    onRenderRow?: (props?: IDetailsRowProps) => JSX.Element;
    onItemClick?: (item: T) => void;
    isHeaderVisible?: boolean;
    groupProps?: IGroupRenderProps;
    groups?: IGroup[];
    announcedMessage?: string;
}
export const CommonDetailsList = <T,>({
    items,
    columns,
    className,
    rowClass,
    queryStatus,
    ariaLabel,
    onItemClick,
    shimmerLines,
    onRenderRow,
    isHeaderVisible = true,
    groupProps,
    groups,
    announcedMessage,
}: CommonDetailsListProps<T>): JSX.Element => {
    const [sentryRef] = useInfiniteScroll({
        loading: !!queryStatus?.isLoading,
        hasNextPage: !!queryStatus?.canLoadMore,
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        onLoadMore: queryStatus?.loadMore ?? (() => {}),
        disabled: !queryStatus,
    });

    const onRenderRowDefault = React.useCallback(
        (props?: IDetailsRowProps): JSX.Element =>
            props ? (
                <DetailsRow
                    {...props}
                    className={mergeStyles(rowClass && rowClass(props))}
                    data-selection-invoke
                    role='row'
                    data-testid='DetailsRow'
                    aria-rowindex={props.itemIndex}
                />
            ) : (
                <></>
            ),
        [rowClass]
    );

    const onRenderDetailsHeader = React.useCallback(
        (props?: IDetailsHeaderProps): JSX.Element | null =>
            props ? (
                <>
                    <DetailsHeader {...props} />
                    {announcedMessage && <Announced message={announcedMessage} />}
                </>
            ) : null,
        [announcedMessage]
    );

    return (
        <ShimmeredDetailsList
            selectionMode={SelectionMode.none}
            constrainMode={ConstrainMode.unconstrained}
            layoutMode={DetailsListLayoutMode.justified}
            enableShimmer={!items}
            columns={columns}
            items={(queryStatus?.isLoading || queryStatus?.canLoadMore) && items ? [...items, null] : items || []}
            isHeaderVisible={isHeaderVisible}
            onRenderRow={onRenderRow ?? onRenderRowDefault}
            onItemInvoked={onItemClick && (item => onItemClick(item))}
            onRenderDetailsFooter={() => <div ref={sentryRef} />}
            className={className}
            ariaLabelForGrid={ariaLabel}
            shimmerLines={shimmerLines}
            role='table'
            groups={groups}
            groupProps={groupProps}
            onRenderDetailsHeader={onRenderDetailsHeader}
        />
    );
};

export const onRenderReportDefinitionColumn = (
    title: string,
    iconBackgroundColor?: string,
    icon?: string,
    description?: string
): JSX.Element => {
    return (
        <CommonIcon
            title={title}
            iconBackgroundColor={iconBackgroundColor}
            icon={icon}
            styles={{ title: { fontWeight: 'inherit' } }}
            description={description}
        />
    );
};
