import React from 'react';
import useInfiniteScroll from 'react-infinite-scroll-hook';

export interface CommonCardListProps {
    items?: JSX.Element[];
    number?: number;
    queryStatus?: { isLoading?: boolean; canLoadMore?: boolean; loadMore?: () => void };
    CardShimmerComponent: JSX.Element;
    shimmerCount?: number;
}

export const CommonCardList: React.FC<CommonCardListProps> = (props: CommonCardListProps): JSX.Element => {
    const { items, CardShimmerComponent, number, queryStatus, shimmerCount = 8 } = props;

    const [sentryRef] = useInfiniteScroll({
        loading: !!queryStatus?.isLoading,
        hasNextPage: !!queryStatus?.canLoadMore,
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        onLoadMore: queryStatus?.loadMore ?? (() => {}),
        disabled: !queryStatus,
    });

    return (
        <>
            {items?.slice(0, number) ??
                Array.from({ length: number ?? shimmerCount }, (_v, i) => (
                    <React.Fragment key={i}>{CardShimmerComponent}</React.Fragment>
                ))}
            {(queryStatus?.isLoading || queryStatus?.canLoadMore) && items && (
                <div ref={sentryRef} style={{ width: '100%' }} aria-hidden>
                    {CardShimmerComponent}
                </div>
            )}
        </>
    );
};
