import { AppHeader } from '@addressbook/components-shared';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { ENVIRONMENT } from '../../../common/env';
import { Strings } from '../../../localization';
import { RefreshButton } from '../RefreshButton/RefreshButton';
import { SearchPeopleField } from '../SearchPeopleField/SearchPeopleField';
import { useAppHeaderStyles } from './AddressBookAppHeader.styles';

export interface AddressBookAppHeaderProps {
    title?: string;
    items?: JSX.Element;
    farItems?: JSX.Element;
    moduleName?: string;
    searchText?: string;
}

export const AddressBookAppHeader = ({
    items,
    farItems,
    title,
    moduleName,
    searchText,
}: AddressBookAppHeaderProps): JSX.Element => {
    const { t } = useTranslation();
    const styles = useAppHeaderStyles();
    const finalFarItems = React.useMemo(() => {
        return !farItems ? (
            <div className={styles.navContainer}>
                <SearchPeopleField searchText={searchText} />
                <RefreshButton />
            </div>
        ) : (
            farItems
        );
    }, [farItems, searchText, styles.navContainer]);

    return (
        <AppHeader
            title={title === undefined ? t(Strings.appName, { context: ENVIRONMENT }) : title}
            items={items}
            farItems={finalFarItems}
            moduleName={moduleName}
        />
    );
};
