import { DefaultButton, PrimaryButton, useTheme } from '@fluentui/react';
import { EmptyState } from '@addressbook/components-shared';
import React from 'react';
import { useTranslation } from 'react-i18next';
import FileEmpty from '../../../assets/emptyStatus/file-empty.svg';
import ItemDisabled from '../../../assets/errorStatus/itemdisabled.svg';
import NewChatImg from '../../../assets/errorStatus/newchat.svg';
import NoAccess from '../../../assets/errorStatus/noaccess.svg';
import OfflineImage from '../../../assets/errorStatus/offline.svg';
import Warning from '../../../assets/errorStatus/warning.svg';
import { Strings } from '../../../localization';
import { LogCategory, logger } from '../../../logger';
import { ClientErrorCodes, RequestError } from '../../../service/RequestError';

export interface ErrorStatusProps {
    img?: string;
    error?: Error;
    errorCode?: SpecialErrorType;
    /**
     * @deprecated use error or errorCode
     */
    title?: string;
    /**
     * @deprecated use error or errorCode
     */
    message?: string;
    /**
     * Prefer associate with error code
     */
    actionText?: string;
    onAction?: () => void;
    className?: string;
    hideImg?: boolean;
    onReturnToHome?: () => void;
}

export enum SpecialErrorType {
    noReport = 'noReport',
    noPermission = 'noPermission',
    newChat = 'newChat',
    guestUser = 'guestUser',
    solutionNotUpgrade = 'SolutionNotUpgrade',
    ReportDefinitionDisabled = 'ReportDefinitionDisabled',
    NotDefinitionSubmitter = 'NotDefinitionSubmitter',
    DefinitionReviewer = 'DefinitionReviewer',
    DefinitionOwner = 'DefinitionOwner',
    FormsResponseNotFound = 'FormsResponseNotFound',
    FormsPrincipalDisabled = 'FormsPrincipalDisabled',
    UserNotEnabled = 'UserNotEnabled',
    NotProvisioned = 'NotProvisioned',
    ExecutedSimilarManualReminderRecently = 'ExecutedSimilarManualReminderRecently',
    Retry = 'retry',
    EnvironmentNotFound = 'EnvironmentNotFound',
    ItemNotFound = 'ItemNotFound',
    BAPBatchAddUserFail = 'BAPBatchAddUserFail',
    MultiUserOperationConflict = 'MultiUserOperationConflict',
}

const ImageMap = {
    [SpecialErrorType.noReport]: FileEmpty,
    [SpecialErrorType.noPermission]: NoAccess,
    [SpecialErrorType.newChat]: NewChatImg,
    [SpecialErrorType.guestUser]: NoAccess,
    [SpecialErrorType.solutionNotUpgrade]: NewChatImg,
    [SpecialErrorType.ReportDefinitionDisabled]: ItemDisabled,
    [SpecialErrorType.NotDefinitionSubmitter]: NoAccess,
    [SpecialErrorType.FormsResponseNotFound]: Warning,
    [SpecialErrorType.FormsPrincipalDisabled]: Warning,
    [SpecialErrorType.NotProvisioned]: NoAccess,
    [ClientErrorCodes.network]: OfflineImage,
    [ClientErrorCodes.teamsSDK]: OfflineImage,
    [SpecialErrorType.DefinitionReviewer]: FileEmpty,
    [SpecialErrorType.DefinitionOwner]: FileEmpty,
    [SpecialErrorType.ItemNotFound]: ItemDisabled,
    [SpecialErrorType.MultiUserOperationConflict]: NewChatImg,
};

/**
 * show error status with localized messages and image
 *
 * @param param0
 * @returns
 */
export const ErrorStatus = ({
    img,
    title,
    message,
    actionText,
    onAction,
    className,
    error,
    errorCode,
    hideImg,
    onReturnToHome,
    children,
}: React.PropsWithChildren<ErrorStatusProps>): JSX.Element => {
    const theme = useTheme();
    const { t } = useTranslation();
    const code = errorCode || (error as RequestError)?.code;
    const image = hideImg ? '' : img || ImageMap[code];
    // using requestId for requestError and {sessionId} for others
    const correlationId = (error as RequestError)?.requestId || `{${logger.sessionId}}`;
    const action = actionText ?? (onAction && t(Strings.errorAction, { context: code }));
    const returnToHomepageAction = code === SpecialErrorType.MultiUserOperationConflict;
    React.useEffect(() => {
        if (error && !(error instanceof RequestError)) {
            // log all display error except for RequestError
            // (Request error already logged)
            logger.logTraceException(LogCategory.ErrorStatus, { error });
        }
    }, [error, code]);

    if (error instanceof RequestError && !message) {
        message = (error.detailedErrors ?? [error])
            .map(errorDetail =>
                t(Strings.errorDescription, {
                    context: errorDetail.code,
                    correlationId,
                    count: errorDetail.additionalErrorMessage?.userNames?.length ?? 0,
                    ...errorDetail.additionalErrorMessage,
                })
            )
            .join('\n');
    }

    return (
        <EmptyState
            image={image}
            role='alert'
            title={title ?? t(Strings.errorTitle, { context: code })}
            message={message}
            actionText={action}
            height='100%'
            background='transparent'
            color={theme.palette.black}
            className={`${className} empty-state`}>
            <div style={{ display: 'flex', justifyContent: 'center', gap: '20px' }}>
                {action && <DefaultButton text={action} onClick={onAction} />}
                {returnToHomepageAction && onReturnToHome && (
                    <PrimaryButton text={t(Strings.returnToHomePage)} onClick={onReturnToHome} />
                )}
            </div>
            {children}
        </EmptyState>
    );
};
