import { mergeStyles, Stack, Text } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import { Spinner } from '@addressbook/components-shared';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { LOADING_TIMEOUT_IN_SECONDS } from '../../../common/Constants';
import { Strings } from '../../../localization';
import { LogCategory, logger } from '../../../logger';
import { useLoadingStyles } from './LoadingPage.styles';

export interface LoadingPageProps {
    className?: string;
    message?: string;
}

export const LoadingPage: React.FC<LoadingPageProps> = ({ message, className }) => {
    const { t, ready } = useTranslation('strings', { useSuspense: false });
    const styles = useLoadingStyles();
    const id = useId('LoadingLabel');

    React.useEffect((): any => {
        const loadingTimeout = setTimeout((): void => {
            logger.logTraceError(
                LogCategory.LoadingTimeout,
                `${LOADING_TIMEOUT_IN_SECONDS} seconds loading timeout happens in page: ${window.location.href}`
            );
        }, LOADING_TIMEOUT_IN_SECONDS * 1000);

        return (): void => clearTimeout(loadingTimeout);
    }, []);

    return (
        <Stack
            verticalFill
            verticalAlign='center'
            horizontalAlign='center'
            className={`${mergeStyles(styles.root, className)} loading-page`}
            role='alert'
            aria-atomic>
            <Spinner aria-label={ready ? t(Strings.loadingAriaLabel) : 'Loading'} aria-describedby={message && id} />
            {message && (
                <Text id={id} className={styles.message}>
                    {message}
                </Text>
            )}
        </Stack>
    );
};
