import { isDarkTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const usePeopleCardStyles = makeStyles(theme => ({
    root: {
        fontSize: theme.fonts.small.fontSize,
        display: 'flex',
        gap: '12px',
        flexDirection: 'column',
    },
    header: {
        display: 'flex',
        flexDirection: 'column',
        gap: '4px',
    },
    main: {
        display: 'flex',
        flexDirection: 'column',
        gap: '12px',
        overflowY: 'auto',
    },
    subHeaderText: {
        fontSize: theme.fonts.medium.fontSize,
        fontWeight: 'bold',
        color: theme.palette.black,
    },
    subHeader: {
        display: 'flex',
        flexDirection: 'row',
        gap: '2px',
        alignItems: 'center',
    },
    presence: {
        display: 'flex',
        flexDirection: 'row',
        gap: '16px',
        alignItems: 'center',
        padding: '12px 16px 12px 16px',
        border: isDarkTheme(theme.name) ? '1px solid #3D3D3D' : '1px solid #F0F0F0',
    },
    ContractInfo: {
        display: 'flex',
        flexDirection: 'row',
        gap: '8px',
        alignItems: 'center',
    },
    icon: {
        width: '20px',
        height: '20px',
        color: theme.palette.black,
        position: 'relative',
        // top: '2px',
        // left: '2px',
    },
    ManagerInfo: {},
    boldText: {
        fontSize: theme.fonts.mediumPlus.fontSize,
        fontWeight: 'bold',
        color: theme.palette.black,
    },
    peopleWithIcon: {
        display: 'flex',
        flexDirection: 'row',
        gap: '16px',
    },
    peopleWithPresenceInfo: {
        position: 'relative',
        width: '64px',
        height: '64px',
    },
    presenceInfo: {
        position: 'absolute',
        bottom: 0,
        borderRadius: '100%',
        border: `2px solid ${theme.palette.white}`,
        backgroundColor: theme.palette.white,
        right: 0,
        width: '20px',
        height: '20px',
    },
    info: {
        display: 'flex',
        flexDirection: 'column',
        gap: '4px',
    },
    quickHeader: {
        display: 'flex',
        flexDirection: 'row',
        gap: '20px',
    },
    callout: {
        width: 320,
        padding: '20px 24px',
    },
    divider: {
        border: isDarkTheme(theme.name) ? '1px solid #3D3D3D' : '1px solid #F0F0F0',
    },
}));
