import { useInfiniteQuery, UseInfiniteQueryOptions, UseInfiniteQueryResult } from 'react-query';
import type { PagedFunctionInClass, ServiceClientClasses } from '../service/gen';
import type { RequestError } from '../service/RequestError';

/**
 * The default size of query page.
 */
const PAGE_SIZE = 10;

/**
 * hook for client page query with parameters,
 * - **never** use `new XXXServiceClient().someMethod`;
 * - use `SERVICE_CLIENT.someMethod` instead.
 *
 * @example
 * ```ts
 *  useReactPageQuery(SERVICE_CLIENT.helloWorld_Get, { a: 'a', b: 1 });
 * ```
 * @param  func SERVICE_CLIENT.method - service client API function @see SERVICE_CLIENT
 * @param  params service client API params
 * @param  options UseInfiniteQueryOptions
 */
function useReactPageQuery<T extends PagedFunctionInClass<ServiceClientClasses>>(
    func: T,
    params: Parameters<T>[0],
    options?: Omit<UseInfiniteQueryOptions<Awaited<ReturnType<T>>, RequestError>, 'queryKey' | 'queryFn'>
): Omit<UseInfiniteQueryResult<Awaited<ReturnType<T>>['results'], RequestError>, 'data'> &
    UseReactPageQueryResult<Awaited<ReturnType<T>>['results']> {
    const res = useInfiniteQuery<Awaited<ReturnType<T>>, RequestError>({
        queryFn: ({ queryKey, signal, pageParam }) => {
            const params = pageParam ? { ...(queryKey[1] as object), skipToken: pageParam } : queryKey[1] ?? {};
            // eslint-disable-next-line @typescript-eslint/no-unsafe-call
            return (func as any)(params, signal);
        },
        queryKey: [func.name, params],
        getNextPageParam: lastPage =>
            lastPage.skipToken && lastPage.results?.length >= (params?.top ?? PAGE_SIZE)
                ? lastPage.skipToken
                : undefined,
        ...options,
    });
    console.log('useReactPageQuery:', res);

    return Object.assign(res, {
        loadMore: () => res.fetchNextPage(),
        canLoadMore: res.isFetched && res.hasNextPage,
        totalCount: res.data?.pages?.[0]?.totalRecordCount,
        data: res.data?.pages
            ?.reduce((pre, cur) => pre.concat(cur.results as any), [])
            .filter(v => v !== null && v !== undefined) as any,
    }) as any;
}

interface UseReactPageQueryResult<T> {
    data?: T;
    loadMore: () => any;
    canLoadMore?: boolean;
    totalCount?: number;
}

export { useReactPageQuery };
export type { UseReactPageQueryResult };
