import React from 'react';

export enum ListComponentStatus {
    Loading = 'loading',
    Error = 'error',
    Empty = 'empty',
    NonEmpty = 'nonEmpty',
}

export const useListComponentStatusChange = (
    items?: unknown[],
    error?: Error | null,
    queryStatus?: { isFetching?: boolean },
    onStatusChange?: (status: ListComponentStatus) => void
): void => {
    const isEmptyList = items ? items.length === 0 : undefined;
    React.useEffect(() => {
        if (onStatusChange) {
            const status = error
                ? ListComponentStatus.Error
                : isEmptyList && !(queryStatus?.isFetching === true)
                ? ListComponentStatus.Empty
                : isEmptyList === false
                ? ListComponentStatus.NonEmpty
                : ListComponentStatus.Loading;
            onStatusChange(status);
        }
    }, [error, isEmptyList, onStatusChange, queryStatus?.isFetching]);
};
